export default async () => {
  const building = localStorage.getItemByName('selectedBuilding')
  console.log(building)
  // const type = JSON.parse(building.buildingType)
  // let res = ''
  // for (const key in this.availableBuildingTypes) {
  //   res += `${key}=${type[key] || ''},`
  // }
  // building.buildingType = res
  console.log(JSON.stringify(building))
  const response = await (await fetch(`${process.env.VUE_APP_BUILDINGS_API_ENDPOINT}/${building._id}`, {
    method: 'PUT',
    headers: {
      'Content-Type': 'application/json',
      Authorization: process.env.VUE_APP_BUILDINGS_API_KEY
    },
    body: JSON.stringify(building)
  })).json()

  console.log('SAVE CURRENT BUILDING DATA:\n', response)

  if (response.error) {
    console.warn(response.error)
    return null
  }

  localStorage.setBuilingsListItem(building._id, building)

  return response.data
}
