export default async (file, path) => {
  const keys = require('@/config/uploadPDF').default

  if (Object.keys(keys).indexOf(path) === -1) throw new Error('Invalid route')
  if (file.type !== 'application/pdf') throw new Error('Invalid file type')

  const formData = new FormData()
  formData.append('file', file)

  console.log(path)
  console.log(formData.get('file'))

  const building = localStorage.getItemByName('selectedBuilding')
  // if (!building.files) building.files = {}
  // if (!building.files[routes[path]]) Object.assign(building.files, { [routes[path]]: {} })

  const response = await (await fetch(`${process.env.VUE_APP_BUILDINGS_API_ENDPOINT}/upload/${path}/${building._id}`, {
    method: 'POST',
    headers: {
      Authorization: process.env.VUE_APP_BUILDINGS_API_KEY
    },
    body: formData
  })).json()

  console.log('UPLOAD BUILDING SITE SERVEY FILE:\n', response)

  if (response.error) {
    // console.warn(response.error)
    // return null
    throw new Error(`Error uploading file: ${file.name}\n${response.error}`)
  }

  // building.files.design.link = response.data
  // localStorage.setBuilingsListItem(building._id, building)

  return response.data
}
